<template>
  <nav>
    <a
      href="javascript:;"
      v-for="(t,i) of nav"
      :key="i"
      :class="{'on':i==SelectedItem}"
      @click="changeSelectItem(i)"
    >{{t}}</a>
    <!-- <a href="javascript:;"></a>
    <a href="javascript:;"></a>
    <a href="javascript:;"></a>
    <a href="javascript:;"></a>
    <a href="javascript:;">App</a>-->
  </nav>
</template>

<script>
export default {
  data() {
    return {
      // 
      nav: ["", "", "", "", "", "App"],
      // 
      SelectedItem: 0
    };
  },
  methods: {
    changeSelectItem(index) {
      this.SelectedItem = index;
    }
  }
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/*  
   ========================================================================== */
nav {
  width: auto;
  background-color: #00b2d6;
  float: left;
  margin: 0 auto;
  padding: 0;
  overflow: hidden;
  height: 40px;
  line-height: 40px;
  min-width: 550px;
  position: relative;
  /* z-index: 99; */
  margin-top: 20px;
  font-size: 16px;
  font-family: Microsoft YaHei, Arial, STXihei, "";
}
/* 
   ========================================================================== */
nav a {
  border: 1px solid #00b2d6;
  float: left;
  margin: auto 8px;
  padding: 0 20px;
  display: block;
  color: #fff;
  height: 30px;
  line-height: 30px;
}
/*  */
nav a.on {
  background-color: #fff;
  color: #00b2d6;
  margin: auto 6px;
  padding: 0 25px;
  border: 1px solid #fff;
  border-radius: 16px;
  height: 30px;
  line-height: 30px;
}
/*  */
nav a:hover {
  border: 1px solid #fff;
  border-radius: 15px;
}
</style>
